ing: $gutter / 2\n\n        .layout:only-child\n          margin: -$gutter / 2\n\n        .layout:not(:only-child)\n          margin: auto (-$gutter / 2)\n\n        *:not(:only-child)\n          .layout:first-child\n            margin-top: -$gutter / 2\n\n          .layout:last-child\n            margin-bottom: -$gutter / 2\n\n.layout\n  display: flex\n  flex: 1 1 auto\n  flex-wrap: nowrap\n  // https://github.com/vuetifyjs/vuetify/issues/3873\n  min-width: 0\n\n  &.reverse\n    flex-direction: row-reverse\n\n  &.column\n    flex-direction: column\n\n    &.reverse\n      flex-direction: column-reverse\n\n    > .flex\n      max-width: 100%\n\n  &.wrap\n    flex-wrap: wrap\n\n  &.grow-shrink-0\n    flex-grow: 0\n    flex-shrink: 0\n\n@each $size, $width in $grid-breakpoints\n  @media all and (min-width: #{$width})\n    $n: $grid-columns\n    @while $n > 0\n      .flex.#{$size}#{$n}\n        flex-basis: $n / $grid-columns * 100%\n        flex-grow: 0\n        max-width: $n / $grid-columns * 100%\n\n      .flex.order-#{$size}#{$n}\n        order: $n\n\n      $n: $n - 1\n\n    $m: $grid-columns\n    @while $m >= 0\n      .flex.offset-#{$size}#{$m}\n        // Offsets can only ever work in row layouts\n        +ltr()\n          margin-left: $m / $grid-columns * 100%\n        +rtl()\n          margin-right: $m / $grid-columns * 100%\n\n      $m: $m - 1\n\n.flex,\n.child-flex > *\n  flex: 1 1 auto\n  max-width: 100%\n\n  &.grow-shrink-0\n    flex-grow: 0\n    flex-shrink: 0\n\n.spacer\n  flex-grow: 1 !important\n\n.grow\n  flex-grow: 1 !important\n  flex-shrink: 0 !important\n\n.shrink\n  flex-grow: 0 !important\n  flex-shrink: 1 !important\n\n.fill-height\n  height: 100%\n","@import '../tools/_functions.sass';\n\n$color-pack: true !default;\n\n$body-font-family: 'Roboto', sans-serif !default;\n$font-size-root: 16px !default;\n$line-height-root: 1.5 !default;\n$border-radius-root: 4px !default;\n\n$rounded: () !default;\n$rounded: map-deep-merge(\n  (\n    0: 0,\n    'sm': $border-radius-root / 2,\n    null: $border-radius-root,\n    'lg': $border-radius-root * 2,\n    'xl': $border-radius-root * 6,\n    'pill': 9999px,\n    'circle': 50%\n  ),\n  $rounded\n);\n\n$spacer: 4px !default;\n\n$spacers: () !default;\n@if (type-of($spacers) == list) {\n  @for $i from 0 through 16 {\n    $spacers: map-merge($spacers, ($i: $spacer * $i))\n  }\n}\n\n$negative-spacers: () !default;\n@if (type-of($negative-spacers) == list) {\n  @for $i from 1 through 16 {\n    $negative-spacers: map-merge($negative-spacers, (\"n\" + $i: -$spacer * $i))\n  }\n}\n\n$grid-breakpoints: () !default;\n$grid-breakpoints: map-deep-merge(\n  (\n    'xs': 0,\n    'sm': 600px,\n    'md': 960px,\n    'lg': 1280px - 16px,\n    'xl': 1920px - 16px\n  ),\n  $grid-breakpoints\n);\n\n$grid-gutter: $spacer * 6 !default;\n$form-grid-gutter: $spacer * 2 !default;\n$grid-columns: 12 !default;\n\n$container-padding-x: $grid-gutter / 2 !default;\n\n$grid-gutters: () !default;\n$grid-gutters: map-deep-merge(\n  (\n    'xs': $grid-gutter / 12,\n    'sm': $grid-gutter / 6,\n    'md': $grid-gutter / 3,\n    'lg': $grid-gutter * 2/3,\n    'xl': $grid-gutter\n  ),\n  $grid-gutters\n);\n\n$container-max-widths: () !default;\n$container-max-widths: map-deep-merge(\n  (\n    'md': map-get($grid-breakpoints, 'md') * 0.9375,\n    'lg': map-get($grid-breakpoints, 'lg') * 0.9375,\n    'xl': map-get($grid-breakpoints, 'xl') * 0.9375\n  ),\n  $container-max-widths\n);\n\n$display-breakpoints: () !default;\n$display-breakpoints: map-deep-merge(\n  (\n    'print-only': 'only print',\n    'screen-only': 'only screen',\n    'xs-only': 'only screen and (max-width: #{map-get($grid-breakpoints, 'sm') - 1})',\n    'sm-only': 'only screen and (min-width: #{map-get($grid-breakpoints, 'sm')}) and (max-width: #{map-get($grid-breakpoints, 'md') - 1})',\n    'sm-and-down': 'only screen and (max-width: #{map-get($grid-breakpoints, 'md') - 1})',\n    'sm-and-up': 'only screen and (min-width: #{map-get($grid-breakp